*{
  margin:0;
  padding:0;
  background-color: #000;
  box-sizing: border-box;
}
.App{
  width: 100%;
  min-height:100vh;
  padding:70px;
  background-size:cover;
  background-position:center;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}
input{
  margin-inline: 1rem;
  margin-block:2rem;
  width:700px;
  padding:15px;
  background-color:whitesmoke;
  font-size: large;
}
button{
  padding:15px;
  font-size:large;
  background-color:rgb(255, 156, 255);
}
.element{
  margin-block:2rem;
  display: grid;
  grid-template-columns: repeat(4,1fr);
  align-items: center;
  justify-content: center;
  gap:4rem;
}
.element img{
  max-width: 250px;
}
.element-div{
  height: 500px;
  border: 3px solid white;
  margin: 0 auto;
  padding: 20px;
}
p{
  margin: 0 auto;
}
.title{
  color: white;
  font-size: large;
}
